import waitImg from '@/assets/common/wait.png'
import waitActiveImg from '@/assets/common/wait-active.png'
import executeImg from '@/assets/common/execute.png'
import executeActiveImg from '@/assets/common/execute-active.png'
import completeImg from '@/assets/common/complete.png'
import completeActiveImg from '@/assets/common/complete-active.png'
import overdueImg from '@/assets/common/overdue.png'
import overdueActiveImg from '@/assets/common/overdue-active.png'
import allImg from '@/assets/common/all.png'
import allActiveImg from '@/assets/common/all-active.png'
import { farmingTypeOptions } from '@/utils/config'

/**
 获取农事列表tab渲染数据    
 @param
 @return { Array } 农事列表tab的渲染数据
 */
export const getAgriculturalRenderTabList = () => {
	return [
		{
			text: '待执行',
			icon: waitImg,
			activeIcon: waitActiveImg,
			type: 0,
			totalDataKey: '0'
		},
		{
			text: '执行中',
			icon: executeImg,
			activeIcon: executeActiveImg,
			type: 1,
			totalDataKey: '1'
		},
		{
			text: '已完成',
			icon: completeImg,
			activeIcon: completeActiveImg,
			type: 2,
			totalDataKey: '2'
		},
		{
			text: '已逾期',
			icon: overdueImg,
			activeIcon: overdueActiveImg,
			type: 3,
			totalDataKey: '3'
		},
		{
			text: '全部',
			icon: allImg,
			activeIcon: allActiveImg,
			type: -1,
			totalDataKey: '-1'
		}
	]
}

/**
 * 茶园的可选数据
 */
const teaGardenOptions = [
	{
		text: '西黄山1号茶园',
		value: '1'
	}
]

/**
 * 茶树品种的可选数据
 */
const breedOptions = [
	{
		text: '当地槠叶种',
		value: '当地槠叶种'
	},
	{
		text: '浙江小叶种',
		value: '浙江小叶种'
	},
	{
		text: '群体中叶种',
		value: '群体中叶种'
	},
	{
		text: '华茶八号',
		value: '华茶八号'
	},
	{
		text: '舒茶早迎霜',
		value: '舒茶早迎霜'
	},
	{
		text: '皖茶六号',
		value: '皖茶六号'
	},
	{
		text: '皖茶七号',
		value: '皖茶七号'
	},
	{
		text: '白茶',
		value: '白茶'
	},
	{
		text: '黄金芽',
		value: '黄金芽'
	}
]

/**
 获取新增农事表单的渲染数据    
 @param 
 @return { Array } 农事表单的渲染数据
 */
export const getAddFormRenderList = () => {
	return [
		{
			label: '选择茶园',
			placeholder: '请选择茶园',
			type: 'select',
			key: 'teaGardenId',
			required: true,
			options: teaGardenOptions
		},
		{
			label: '茶园工',
			placeholder: '请选择茶园工',
			type: 'select',
			key: 'operatePerson',
			required: true,
			options: []
		},
		{
			label: '农事类型',
			placeholder: '请选择农事类型',
			type: 'select',
			key: 'farmingType',
			required: true,
			options: farmingTypeOptions
		},
		{
			label: '农事描述',
			placeholder: '请填写农事详细描述',
			type: 'textarea',
			key: 'farmingRemark',
			required: true
		},
		{
			label: '茶树品种',
			placeholder: '请输入茶树品种',
			type: 'select',
			key: 'breed',
			required: true,
			isHide: true,
			options: breedOptions
		},
		{
			label: '完成时间',
			placeholder: '请选择完成时间',
			type: 'dateTimePicker',
			key: 'estimateEndTime',
			required: true
		}
	]
}


