<template>
    <FcPage title="农事工单">
        <div class="agriculturalList">
            <div class="agricultural-top">
                <FcActionSheet placeholder="筛选农事类型" :options="farmingTypeOptions" title="农事类型" v-model="farmingTypeVlaue" @change="handleFarmingTypeChange"></FcActionSheet>
            </div>
            <FcAgriculturalTab v-model="tabIndex" :renderList="agriculturalRenderTabList" @change="handleChangeTab" :totalData="totalData"></FcAgriculturalTab>

            <div class="agricultural-box">
                <FcAgriculturalList isEdit ref="fcAgriculturalList" :loaded="loaded" :list="agriculturalList" @scrolltolower="handleScrolltolower" @delete="handleDelete" @edit="handleEdit"></FcAgriculturalList>
            </div>

            <img class="agricultural-addImg" src="@/assets/common/add.png" alt="" @click="handleAddAgricultural">
        </div>
    </FcPage>
</template>

<script>
import FcAgriculturalTab from '@/components/agriculturalTab/agriculturalTab.vue'
import { getAgriculturalRenderTabList } from './config'
import FcAgriculturalList from '@/components/agriculturalList/agriculturalList.vue'
import { farmingTypeMap, farmingTypeOptions } from '@/utils/config'
import { enterpriseUserDataStorage, agriculturalTabStorage } from '@/utils/storage'
import FcActionSheet from "@/components/actionSheet/actionSheet.vue"
import { isEmpty } from '@/utils/common'
export default {
    name: 'agriculturalList',

    components: {
        FcActionSheet,
        FcAgriculturalTab,
        FcAgriculturalList
    },

    data() {
        return {
            tabIndex: 0,
            agriculturalRenderTabList: getAgriculturalRenderTabList(),
            userData: enterpriseUserDataStorage.getUserData(),
            farmingTypeOptions: [{ text: '全部', value: "-1" }].concat(farmingTypeOptions),
            agriculturalList: [],
            page: 1,
            currPage: 0,
            count: 0,
            limit: 15,
            loaded: false,
            farmingTypeVlaue: -1,
            totalData: {}
        }
    },

    created() {
        this.tabIndex = agriculturalTabStorage.getTabIndex() || 0
        agriculturalTabStorage.removeTabIndex()

    },

    mounted() {
        this.getAgriculturalList()
        this.getAgriculturalTotalData()
    },

    methods: {
        /**
            获取农事列表统计数据
            @param 
            @return
        */
        getAgriculturalTotalData() {
            const params = {
                tenantId: this.userData.tenantId,
                assignPerson: this.userData.userId
            }
            if (this.farmingTypeVlaue != -1) {
                params.farmingType = this.farmingTypeVlaue
            }
            this.$axios({
                url: '/teaAction/getCountByUserId',
                data: params
            }).then((res) => {
                if (!isEmpty(res.data)) {
                    res.data['-1'] = res.data['0'] + res.data['1'] + res.data['2'] + res.data['3']
                    this.totalData = res.data
                } else {
                    this.totalData = {}
                }
            }).catch(() => {
                this.totalData = {}
            })
        },
        /**
            获取农事列表
            @param { Function } callback 取消节流的回调函数
            @return
        */
        getAgriculturalList(callback) {
            this.loaded = false
            const params = {
                page: this.page,
                limit: this.limit,
                assignPerson: this.userData.userId,
                tenantId: this.userData.tenantId
            }
            if (this.tabIndex != -1) {
                params.operateStatus = this.tabIndex
            }
            if (this.farmingTypeVlaue != -1) {
                params.farmingType = this.farmingTypeVlaue
            }
            this.$axios({
                url: '/teaAction/getFarmingList',
                data: params
            }).then((res) => {
                if (Array.isArray(res.data)) {
                    const agriculturalList = res.data.map((item) => {
                        return {
                            ...item,
                            farmingName: farmingTypeMap[item.farmingType],
                        }
                    })
                    if (this.page == 1) {
                        this.agriculturalList = agriculturalList
                    } else {
                        this.agriculturalList = this.agriculturalList.concat(agriculturalList)
                    }
                } else {
                    if (this.page == 1) {
                        this.agriculturalList = []
                    }
                }
                this.currPage = this.page
                this.count = res.count || 0
                this.loaded = true
                callback && typeof callback === 'function' && callback()
            }).catch(() => {
                this.count = 0
                this.loaded = true
                if (this.page == 1) {
                    this.agriculturalList = []
                }
                callback && typeof callback === 'function' && callback()
            })
        },

        /**
            tab项改变时触发
            @param
            @return
        */
        handleChangeTab() {
            this.page = 1
            const fcAgriculturalListEle = this.$refs.fcAgriculturalList
            fcAgriculturalListEle.onRefresh && typeof fcAgriculturalListEle.onRefresh === 'function' && fcAgriculturalListEle.onRefresh()
            this.getAgriculturalList()
        },
        /**
            处理滚动到底部事件
            @param { Function } callback 取消节流的回调函数
            @return
        */
        handleScrolltolower(callback) {
            if (this.page * this.limit < this.count) {
                this.page++
                this.getAgriculturalList(callback)
            } else {
                callback()
            }
        },

        /**
           处理农事类型改变事件
           @param 
           @return
       */
        handleFarmingTypeChange() {
            this.page = 1
            const fcAgriculturalListEle = this.$refs.fcAgriculturalList
            fcAgriculturalListEle.onRefresh && typeof fcAgriculturalListEle.onRefresh === 'function' && fcAgriculturalListEle.onRefresh()
            this.getAgriculturalList()
            this.getAgriculturalTotalData()
        },

        /**
            处理点击删除时触发
            @param { Event } objecct 子组件传递的事件参数
            @return
        */
        handleDelete({ key, item, callback }) {
            this.$axios({
                url: '/teaAction/delFarming',
                method: 'GET',
                data: {
                    id: item.id
                }
            }).then((res) => {
                this.$toast("删除成功")
                this.agriculturalList.splice(key, 1)
                callback()
            })
        },

        /**
            去编辑农事项
            @param { Object } item 农事项
            @return
        */
        handleEdit(item) {
            agriculturalTabStorage.setTabIndex(this.tabIndex)
            this.$router.push({
                path: '/enterprise/agriculturalManage/add',
                query: {
                    id: item.id
                }
            })
        },
        /**
            点击新增农事时触发
            @param 
            @return
        */
        handleAddAgricultural() {
            agriculturalTabStorage.setTabIndex(this.tabIndex)
            this.$router.push('/enterprise/agriculturalManage/add')
        }
    },

}

</script>

<style scoped lang='scss'>
.agriculturalList {
    position: relative;
    min-height: 100%;
    background-color: #f5f5f5;
    /deep/ .agricultural-tabs {
        padding-top: 0.26rem;
        border-top: none;
        .agricultural-tabItem {
            .agricultural-tabItem-img {
                width: 0.92rem;
                height: 0.92rem;
            }
        }
    }

    .agricultural-top {
        padding: 0.08rem 0.3rem 0;
        background-color: #fff;
    }
    .agricultural-box {
        position: absolute;
        top: 3.5rem;
        left: 0rem;
        right: 0rem;
        bottom: 0;
    }

    .agricultural-addImg {
        position: fixed;
        right: 0.3rem;
        bottom: 0.6rem;
        width: 1.04rem;
        height: 1.04rem;
    }
}
</style>


